/**
 *  URL API
 * @constant {string}
 */
const BASE_URL = 'http://213.219.214.83:8080';

/**
 *  HTTP-   URL
 * @async
 * @param {string} url - URL 
 * @param {string} method - HTTP- (GET, POST, DELETE  . .)
 * @param {Object} [data=null] -   ( )
 * @returns {Promise<Object>}     { success: boolean, data?: any, message?: string }
 */
async function sendRequest(url, method, data = null) {
	try {
		const options = {
			method: method,
			credentials: 'include',
			headers: {
				'Content-Type': 'application/json'
			}
		};

		if (data) {
			options.body = JSON.stringify(data);
		}

		const response = await fetch(url, options);

		if (response.ok) {
			const responseData = await response.json();
			return { success: true, data: responseData };
		}
		const errorText = await response.text();
		return { success: false, message: errorText };
	} catch (error) {
		return { success: false, message: error.message };
	}
}

/**
 *   
 * @async
 * @param {string} login -  
 * @param {string} password -  
 * @returns {Promise<Object>}  
 */
async function loginUser(login, password) {
	const url = `${BASE_URL}/users`;
	return await sendRequest(url, 'POST', { login, password });
}

/**
 *  
 * @async
 * @param {string} login -  
 * @param {string} password -  
 * @returns {Promise<Object>}  
 */
async function authUser(login, password) {
	const url = `${BASE_URL}/users/login`;
	return await sendRequest(url, 'POST', { login, password });
}

/**
 *   ,   
 * @async
 * @param {number} id -  
 * @returns {Promise<Object>}  
 */
async function getProfiles(id) {
	const url = `${BASE_URL}/profiles?forUser=${id}`;
	return await sendRequest(url, 'GET');
}

/**
 *    
 * @async
 * @param {number} userId -  
 * @returns {Promise<Object>}  
 */
async function getProfile(userId) {
	const url = `${BASE_URL}/profiles/${userId}`;
	return await sendRequest(url, 'GET');
}

/**
 *   
 * @async
 * @returns {Promise<Object>}  
 */
async function logoutUser() {
	const url = `${BASE_URL}/users/logout`;
	return await sendRequest(url, 'POST');
}

/**
 *  
 * @async
 * @param {number} userId -  
 * @returns {Promise<Object>}  
 */
async function deleteUser(userId) {
	const url = `${BASE_URL}/users/${userId}`;
	return await sendRequest(url, 'DELETE');
}

/**
 * ,    
 * @async
 * @returns {Promise<Object>}  
 */
async function checkSession() {
	const url = `${BASE_URL}/users/checkSession`;
	return await sendRequest(url, 'GET');
}

export default {
	BASE_URL,
	getProfiles,
	authUser,
	loginUser,
	getProfile,
	logoutUser,
	deleteUser,
	checkSession,
};
